import React,{useRef} from 'react';
import {  gql,useQuery } from '@apollo/client';
import './App.css'

const TEST_QUERY = gql`
  query EntityExample($entityid: String!) {
    entity (id: $entityid context: "Context1" workspace: "Main") {
      id
      title
      values{
        attribute{title}
        simpleValue
      }
    }
  }
`;

export default function Entity({id}) {
    const queryParam = new URLSearchParams(window.location.search)
    const p_id = queryParam.get("id")
    //setmethod();

    var entityid = p_id;
    if(entityid === null){
      entityid = "_"
    }
    
    const { loading, error, data, refetch } = useQuery(TEST_QUERY,{variables: {entityid: entityid} });
    const inputRef = useRef()

    
    
    if (loading) return <p>Loading...</p>;
    if (error){
      //localStorage.removeItem("token");
      //setCookie("token",null)
      return <p>Error...</p>
    }  



    function handleSearchbutton(){
      refetch({entityid: inputRef.current.value})
    }

    function handleEnter(){
      if(window.event.keyCode === 13){
        handleSearchbutton();
      }
    }
    
    return (
      <div>
        <table  style={{width: '50%', borderStyle:'solid', borderColor:'#65a839'}}>
          <thead>
            <tr>
              <th align='center'>EntityID</th> 
              <th><input type='text' ref={inputRef} onKeyUp={handleEnter}/></th>
              <th><button className='btn-default' onClick={handleSearchbutton}>Search</button></th>
            </tr>
          </thead>
          <tbody>
            {
              data.entity != null?
              <>
                <tr>
                  <td><b>ID</b></td>
                  <td>{data.entity.id}</td>
                </tr>
                <tr>
                  <td><b>NAME</b></td>
                  <td>{data.entity.title}</td>
                </tr>
                {data.entity.values.map(
                    (value) => (
                        <tr>
                          <td><b>{value.attribute.title}</b></td>
                          <td>{value.simpleValue}</td>
                        </tr>
                      )
                  )
                }
              </>
              :<div>Not found Data.</div> 
            }  
          </tbody>  
        </table>
      </div>
    );
}
