import React, { useEffect, useState } from "react";
import {  gql,useQuery } from '@apollo/client';
import { useNavigate } from "react-router-dom";

const EntityTree = ({id,name,lvl}) => {
    var ENTITY_QUERY = gql`
        query entityTree($entityid: String!) {
            searchForEntities(context: "Context1", workspace: "Main", condition: {id: {queryString: $entityid, operator: eq}}) {
                pageElements {
                    id
                    name
                    children {
                        pageElements {
                            id
                            name
                        }
                    }
                }
            }
        }`;
    

    const navigate = useNavigate()
    //const { loading, error, data, refetch } = useQuery(ENTITY_QUERY,{variables: {entityid: ""} });
    const { loading, data, refetch } = useQuery(ENTITY_QUERY);
    const [childData, setChildData] = useState([]);
    const [showChild, setShowChild] = useState(false)
    useEffect(() =>{
        var local_data = localStorage.getItem(id)
        var local_showchild = Boolean(localStorage.getItem(id+"_showchild")==="true"?true:false)
        if(local_data !== null){
            local_data = JSON.parse(local_data)
        }
        
        if(data !== undefined){
            localStorage.setItem(id,JSON.stringify(data))
            setChildData(data.searchForEntities.pageElements[0].children.pageElements)
            //setShowChild(true)
        }else if(local_data !== null){
            setChildData(local_data.searchForEntities.pageElements[0].children.pageElements)
            setShowChild(local_showchild)
        }
    },[data])

    
    if (loading) return <tr><td>Loading...</td></tr>;

    
    
    const searchChildren = () => {
        if(!showChild){
            refetch({entityid: id})
            setShowChild(true)
            localStorage.setItem(id+"_showchild",true)
        }else{
            setShowChild(false)
            localStorage.setItem(id+"_showchild",false)
        }
        
    }

    const searchEntity = (e) =>{
        navigate("/entity?id=" + e.target.id)
    }

    

    const marginleft = (Number(lvl)*50) + "px";
    const background = showChild?"#49fcd4":"white";
    
    return (
    <>
            <tr style={{textAlign:'start'}}>
                <td>
                    <div style={{fontSize:"30px",marginLeft:marginleft}}>
                        <b onClick={searchChildren}>{showChild?" - ":" + "}</b>
                        <button className="btn-secondary" onClick={searchChildren}>{id} - {name}</button>
                        <button id={id} className="btn-default" onClick={searchEntity}>상세</button>
                    </div>                    
                </td>
            </tr>
            {showChild?childData.map(child => (<EntityTree key={child.id}  id={child.id} name={child.name} lvl={Number(lvl)+1}/>)):""}
    </>
    );

}

export default EntityTree;