
import { render } from 'react-dom';
import { ApolloClient, createHttpLink, InMemoryCache,ApolloProvider, ApolloLink} from '@apollo/client';
import { setContext } from '@apollo/client/link/context';
import {onError} from '@apollo/client/link/error'
import './css/css-app-green.css';
import { BrowserRouter } from 'react-router-dom';
import App from "./App"


const stepurl = "apacpresales-hmckia-step.mdm.stibosystems.com"
localStorage.setItem('stepurl',stepurl)
const httpLink = createHttpLink({
  uri: 'https://' + stepurl + '/graphqlv2/graphql',
});


const authLink = setContext((_, { headers }) =>  {
  // get the authentication token from local storage if it exists
  const token = localStorage.getItem("token");
  //console.log(token.value)
  return {
    headers: {
      authorization: token ? `Bearer ${token}` : "",
    }
  }
});


const handleError = onError(({response, networkError}) => {
  if(networkError){
    //setCookie("token", null)
    //window.location.reload()
  }
})

const client = new ApolloClient({
  link: ApolloLink.from([authLink.concat(httpLink),handleError]) ,
  cache: new InMemoryCache()
});



render(
  <ApolloProvider client={client}>
    <BrowserRouter>
      <App />          
    </BrowserRouter> 
  </ApolloProvider>
  ,
  document.getElementById('root'),
);


