import React, {useRef} from 'react'
import axios from 'axios';
import qs from 'qs'


export default function Login() {
    const idRef = useRef()
    const pwRef = useRef()

    

    function handleLoginClick(){
        const inputparms = {
            userId: idRef.current.value,
            password: pwRef.current.value
        }

        const stepurl = localStorage.getItem('stepurl')
        axios.post("https://" + stepurl + "/graphqlv2/auth",qs.stringify(inputparms),{
            headers: {
                "Content-Type": `application/x-www-form-urlencoded`,
                "Access-Control-Allow-Origin": stepurl
                }
        }).then(res => {
            if(res.status === 200){
                localStorage.setItem("token", res.data);
                window.location.reload();  
            }else{
                alert(res.data)
            }
            
        }).catch(err =>{
            alert(err)
        });
    }

    function handleEnter(){
        if(window.event.keyCode === 13){
            handleLoginClick();
        }
    }
    return (
    <div align="center" style={{marginTop:"10%"}}>
        <div className='alt'>
            <h1>STEP LOGIN</h1>
        </div>
        <div style={{float: 'top'}}>
            <div><input type="text" style={{width: "20%"}} ref={idRef} onKeyUp={handleEnter} placeholder="ID"></input></div>
            <div><input type="password" style={{width: "20%"}} ref={pwRef} onKeyUp={handleEnter} placeholder="PASSWORD"></input></div>
            <div><button className='btn-primary' style={{width: "20%"}}  type='button' onClick={handleLoginClick}>Login</button></div>
        </div>
        
    </div>
    
    )
}
