import React, { useEffect, useState } from "react";
import {  gql,useQuery } from '@apollo/client';
import { useNavigate } from "react-router-dom";

const ProductTree = ({id,name,lvl}) => {
    var PRODUCT_QUERY = gql`
        query productTree($prodid: String!) {
            searchForProducts(context: "Context1", workspace: "Main", condition: {id: {queryString: $prodid, operator: eq}}) {
                pageElements {
                    id
                    name
                    children {
                        pageElements {
                            id
                            name
                        }
                    }
                }
            }
        }`
    ;
    //const queryParam = new URLSearchParams(window.location.search)
    
    const navigate = useNavigate()
    //const { loading, error, data, refetch } = useQuery(PRODUCT_QUERY,{variables: {prodid: "_"} });
    const { loading, data, refetch } = useQuery(PRODUCT_QUERY);
    const [childData, setChildData] = useState([]);
    const [showChild, setShowChild] = useState(false);
    

    useEffect(() =>{
        var local_data = localStorage.getItem(id)
        var local_showchild = Boolean(localStorage.getItem(id+"_showchild")==="true"?true:false)
        if(local_data !== null){
            local_data = JSON.parse(local_data)
        }
        
        if(data !== undefined){
            localStorage.setItem(id,JSON.stringify(data))
            setChildData(data.searchForProducts.pageElements[0].children.pageElements)
            //setShowChild(true)
        }else if(local_data !== null){
            setChildData(local_data.searchForProducts.pageElements[0].children.pageElements)
            setShowChild(local_showchild)
        }
    },[data])

    
    if (loading) return <tr><td>Loading...</td></tr>;

        
        
    const searchChildren = () => {
        if(!showChild){
            refetch({prodid: id})
            setShowChild(true)
            localStorage.setItem(id+"_showchild",true)
        }else{
            setShowChild(false)
            localStorage.setItem(id+"_showchild",false)
        }
        
    }

    const searchProduct = (e) =>{
        navigate("/product?id=" + e.target.id)
    }

    const marginleft = (Number(lvl)*50) + "px";
    const background = showChild?"#49fcd4":"white";
    
    return (
    <>
            <tr style={{textAlign:'start'}}>
                <td>
                    <div style={{fontSize:"30px",marginLeft:marginleft}}>
                        <b  onClick={searchChildren} >{showChild?" - ":" + "}</b>
                        <button className="btn-secondary" onClick={searchChildren}>{id} - {name}</button>
                        <button id={id} className="btn-default" onClick={searchProduct}>상세</button>
                    </div>
                    
                </td>
            </tr>
            {showChild?childData.map(child => (<ProductTree key={child.id} id={child.id} name={child.name} lvl={Number(lvl)+1}/>)):""}
    </>
    );

}

export default ProductTree;