import React, {useEffect, useState} from 'react'
import Product from './Product'
import Entity from './Entity'
import { Link, Route, Routes } from 'react-router-dom'
import ProductTree from './pages/ProductTree'
import EntityTree from './pages/EntityTree'


export default function Header() {
    // var pathmap = new Map()
    // pathmap.set("/producttree","1")
    // pathmap.set("/entitytree","2")
    // pathmap.set("/product","3")
    // pathmap.set("/entity","4")
    // const tempgubun = pathmap.get(pathname)
    // if(tempgubun === null){
    //     tempgubun = "1"
    // }

    

    const pathname = window.location.pathname;
    const [currentProduct,setProduct] = useState()
    const [currentEntity,setEntity] = useState()
    const [gubun, setGubun] = useState("1");

    function handleLogout(){
        window.localStorage.clear();
        window.location.reload()
    }

    const goProductTree = () =>{        
        setGubun("1");
    }

    const goEntityTree = () =>{
        setGubun("2");
    }

    const goProduct = () =>{
        setGubun("3");
    }


    const goEntity = () =>{
        setGubun("4");
    }   
    
    
    return(
        <div style={{marginLeft:'10px', textAlign:'start'}}>
            <div>
                <h1>STEP Search Result
                <button style={{marginLeft: '5%'}} className='btn-default' onClick={handleLogout}>Logout</button>
                </h1>
                
            </div>        
            <div>
                <h2>
                    <Link to="/" onClick={goProductTree}  className="btn-primary">Product Tree</Link>
                    <Link to="/entitytree"  onClick={goEntityTree} className="btn-primary">Entity Tree</Link>
                    <Link to="/product"     onClick={goProduct}      className="btn-primary">Product Search</Link>
                    <Link to="/entity"      onClick={goEntity}       className="btn-primary">Entity Search</Link>
                </h2>
                
            </div>
            <Routes>
                <Route path="/"  element={<table><tbody><ProductTree key="Product hierarchy root" id="Product hierarchy root" name="Product hierarchy root" lvl="0"/></tbody></table>}/>
                <Route path="/entitytree"   element={<table><tbody><EntityTree key="Entity hierarchy root"   id="100305"  name="Entity root"lvl="0"/></tbody></table>}/>
                <Route path="/product"      element={<Product id={currentProduct} />}/>
                <Route path="/entity"       element={<Entity id={currentEntity} />}/>
            </Routes>
        </div>
    )
    
}
