import React,{useRef} from 'react';
import {  gql,useQuery } from '@apollo/client';
import './App.css'
import { useNavigate } from 'react-router-dom';


var TEST_QUERY = gql`
  query productExample($prodid: String!) {
    product (id: $prodid context: "Context1" workspace: "Main") {
      id
      title
      objectType {
        id
        title
      } 
      values{
        attribute{title}
        simpleValue
      }
    }
  }
  `;

export default function Product({id}) {
    const queryParam = new URLSearchParams(window.location.search)
    const p_id = queryParam.get("id")
    const navigate = useNavigate()
    //setmethod();

    var prodid = p_id;
    if(prodid === null){
      prodid = "_"
    }
    const { loading, error, data, refetch } = useQuery(TEST_QUERY,{variables: {prodid: prodid} });
    const inputRef = useRef()
    //const [searchResult, setResult] = useState({data})

    
    
    if (loading) return <p>Loading...</p>;
    if (error){
      //localStorage.removeItem("token");
      //setCookie("token",null)
      return <p>Error...</p>
    }  

 


    function handleSearchbutton(){
      refetch({prodid: inputRef.current.value})
    }

    function handleEnter(){
      if(window.event.keyCode === 13){
        handleSearchbutton();
      }
    }
        
    return (
      <div>
        <table style={{width: '50%', borderStyle:'solid', borderColor:'#65a839'}}>
          <thead>
            <tr>
              <th align='center'>ProductID</th> 
              <th><input type='text' ref={inputRef} onKeyUp={handleEnter}/></th>
              <th><button className='btn-default' onClick={handleSearchbutton}>Search</button></th>
            </tr>            
          </thead>
          <tbody>
            {
              data.product != null?
              <>
                <tr>
                  <td><b>ID</b></td>
                  <td>{data.product.id}</td>
                </tr>
                <tr>
                  <td><b>NAME</b></td>
                  <td>{data.product.title}</td>
                </tr>
                {data.product.values.map(
                    (value) => (
                        <tr>
                          <td><b>{value.attribute.title}</b></td>
                          <td>{value.simpleValue}</td>
                        </tr>
                      )
                  )
                }
              </>
              :<tr>
                  <td>Not found Data.</td>
               </tr> 
            }  
          </tbody>  
        </table>
      </div>
    );
}
